dth:25%;\n    margin: 10px;\n    height:100%;\n    box-shadow: 3px 3px 5px rgb(88, 88, 88);\n    border-radius: 7px;\n}\n/* Hello and Search div */\n.top-page-div{\n    margin:2px;\n    padding: 2px;\n}\n/* Hello title design */\n.hello-class{\n    display: inline-block;\n    position: absolute;\n    left: 90px;\n    padding:2px;\n    margin: 2px;\n    font-family: myTitle;\n    font-size: 35px;\n    color: white;\n}\n/* Search div design */\n.search-class{\n    display: inline-block;\n    padding:2px;\n    margin: 2px;\n}\n/* Sorry Title design - when no search results were found */\n.sorry{\n    color: white;\n    font-family: myTitle;\n    font-size: 40px;\n    padding: 0;\n    margin: 0;\n}\n/* Text design - when no search results were found */\n.no-result{\n    color: white;\n    padding: 0;\n    margin: 0;\n    font-size: 18px;\n    text-shadow: 2px 2px 3px rgb(24, 6, 14);\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Hello title design */\n    .hello-class{\n        margin: auto;\n        padding: 0;\n        font-size: 30px;\n        display: block;\n        width: 80%;\n        position: relative;\n        left: auto;\n    }\n\n    /* Search div design */\n    .search-class {\n        display: block;\n        margin: 3px;\n        width: 80%;\n    }\n\n    /* container div - containing cart and products section */\n    .container{\n        display: grid;\n        grid-template-columns: repeat(1, 1fr);\n    }\n\n    /* Cart div */\n    .cart-div{\n        grid-row:2;\n        width: 85%;\n        height:330px;\n    }\n\n    /* Products and Categories div */\n    .products-master-div{\n        width: 85%;\n        height: 330px;\n        margin-left:3px ;\n        grid-row:1;\n        float: none;\n    }\n\n    /* Categories div */\n    .categories-div{\n        height: 20%;\n    }\n\n    /* Product div design */\n    .product-div{\n        width: 175px;\n    }\n    \n    /* Product div hover design */\n    .product-div:hover{\n        width:175px;\n    }\n}\n/* Design for screen larger than 575px and smaller than 991px (including 991px) */\n@media screen and (max-width: 991px) and (min-width: 576px) {\n\n    /* Cart div */\n    .cart-div{\n        width: 35%;\n        height:500px;\n    }\n\n    /* Products and Categories div */\n    .products-master-div{\n        width: 55%;\n        height: 500px;\n        margin-left: 5px;\n    }\n\n    /* Product div design */\n    .product-div{\n        width: 160px;\n        height: 140px;\n    }\n    \n    /* Product div hover design */\n    .product-div:hover{\n        width:160px;\n        height: 140px;\n    }\n\n    /* Product text design */\n    .product-p{\n        font-size: 10px;\n    }\n\n    /* Search div design */\n    .search-class{\n        padding-left: 100px;\n    }\n\n    /* Top page div */\n    .master-div{\n        height: 100%;\n    }\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"