export default "/* Button design */\nbutton{\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* Modal container div */\n.modal-container{\n    background-image: linear-gradient( 135deg, rgb(58, 16, 16), rgb(107, 107, 107),rgb(133, 133, 133),rgb(107, 107, 107),rgb(58, 16, 16));\n    background-size: cover;\n    background-repeat: no-repeat;\n    text-align: center;\n    border-radius: 3px;\n}\n/* Modal footer */\n.modal-footer{\n    border: none;\n    text-align: center;\n}\n/* Title design */\n.title{\n    color: white;\n    text-shadow: 2px 2px 3px rgb(41, 11, 23);\n    font-family: myTitle;\n    font-size: 40px;\n    padding: 0;\n    margin: 0;\n}\n/* Text design */\n.text{\n    color: white;\n    text-shadow: 2px 2px 3px rgb(41, 11, 23);\n    font-size: 15px;\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n    \n    /* Title design */\n    .title{\n        color: white;\n        text-shadow: 2px 2px 3px rgb(41, 11, 23);\n        font-family: myTitle;\n        font-size: 30px;\n        padding: 0;\n        margin: 0;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"