export default "/* Nav design */\nnav{\n    display: inline-block;\n}\n/* Div design */\ndiv{\n    display: inline-block;\n}\n/* \"Navigation bar\" - logout, home, ans span design */\n.logout-button, a, span{\n    color: rgb(255, 255, 255);\n    text-decoration: none;\n    font-size: 15px;\n}\n/* Logout button design */\n.logout-button{\n    margin: 0;\n    padding: 0;\n    padding-bottom: 4px;\n    font-size: 15px;\n}\n/* Logo image */\nimg{\n    width:30px;\n    height:25px;\n    display: block;\n    -webkit-filter: invert(100%) drop-shadow(2px 2px 3px rgb(20, 3, 9));\n            filter: invert(100%) drop-shadow(2px 2px 3px rgb(20, 3, 9));\n    margin: auto;\n}\n/* Site name- part of logo design */\np{\n    color: white;\n    text-shadow: 2px 2px 3px rgb(22, 5, 12);\n    font-family: myTitle;\n    font-size: 27px;\n    margin: 0;\n}\n/* Logo div */\n.logo-div{\n    display: block;\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Logo image */\n    img {\n        width: 25px;\n        height: 20px;\n    }\n\n    /* Site name- part of logo design */\n    p{\n        font-size: 20px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"