export default "/* Cart's div */\n.shopping-list-div{\n    height: 80%;\n}\n/* Shopping list */\n.shopping-list{\n    font-size: 14px;\n    display: inline-block;\n    color:white;\n    text-shadow: 1px 1px 2px rgb(0, 0, 0);\n}\n/* Back to shop button */\n.back-button{\n    color: white;\n    text-decoration: underline;\n}\n/* Trash icon */\n.trash-icon{\n    width: 25px;\n    height: 25px;\n    -webkit-filter:brightness(400%) ;\n            filter:brightness(400%) ;\n}\n/* Trash icon hover */\n.trash-icon:hover{\n    width: 28px;\n    height: 28px;\n    -webkit-filter:brightness(500%) ;\n            filter:brightness(500%) ;\n}\n/* td where trash icon is */\n.trash-td{\n    width: 40px;\n    height: 40px;\n}\n/* table, tr, td design: */\ntable, tr, td{\n    margin:5px;\n    padding: 2px;\n    text-align: center;\n}\n/* Section where all the carts items are at */\n.cart-items-div{\n    text-align: center;\n    overflow-y: auto;\n    height:75%;\n    box-shadow: inset 0 0 20px rgba(37, 9, 21, 0.705);\n    background-color: rgba(46, 46, 46, 0.075);\n    border-radius: 5px;\n    padding: 3px;\n}\n/* Cart title */\n.cart-title{\n    color: white;\n    font-family: myTitle;\n    font-size: 30px;\n    padding: 0;\n    margin: 0;\n    text-shadow: 1px 1px 2px rgb(46, 45, 45);\n}\n/* Bottom div where buttons and total amount are at */\n.bottom-div{\n    position: absolute;\n    height:22%;\n    width: 20%;\n    padding: 3px;\n    margin-top: 5px;\n    margin-left: 5px;\n}\n/* Product image design  */\n.product-image{\n    width: 40px;\n    height: 40px;\n    -webkit-filter: drop-shadow(2px 2px 3px rgb(29, 5, 14));\n            filter: drop-shadow(2px 2px 3px rgb(29, 5, 14));\n}\n/* Total amount design */\n.total-p{\n    font-size:28px;\n    margin:2px;\n    font-family: myTitle;\n    color: white;\n    text-shadow: 2px 2px 3px rgb(20, 5, 10);\n}\n/* Buttons design */\n.cart-button{\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* Buttons desabled design */\n.cart-button:disabled{\n    margin:0;\n    color: rgb(146, 146, 146);\n    background-image: radial-gradient( rgb(121, 93, 101), rgb(88, 54, 63));\n    border-radius: 5px;\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Bottom div where buttons and total amount are at */\n    .bottom-div{\n        position: relative;\n        height:20%;\n        width: 90%;\n    }\n}\n/* Design for screen larger than 575px and smaller than 991px (including 991px) */\n@media screen and (max-width: 991px) and (min-width: 576px) {\n\n    /* Bottom div where buttons and total amount are at */\n    .bottom-div{\n        position: relative;\n        height:20%;\n        width: 80%;\n    }\n}\n\n\n\n\n/*# sourceMappingURL=data:application/json;base64, */"