export default "/* Button design */\n.register-button{\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* Button disabled design */\n.register-button:disabled{\n    margin:0;\n    color: rgb(146, 146, 146);\n    background-image: radial-gradient( rgb(121, 93, 101), rgb(88, 54, 63));\n    border-radius: 5px;\n}\n/* Table,tr,td design */\ntable, tr, td{\n    margin: auto;\n}\n/* Input and select design */\ninput, select{\n    width:190px;\n    margin: 7px;\n    height: 26px;\n    border-radius: 3px;\n    box-shadow: 3px 2px 5px rgb(53, 22, 31);\n    border: none;\n    padding: 3px;\n    background-color: rgba(255, 255, 255, 0.589);\n}\n/* td design with inputs */\n.input-td{\n    padding-left: 10px;\n}\n/* Error message design */\n.err-msg{\n    color: white;\n    display: block;\n    font-weight: 500;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding-left: 0px 3px;\n    font-size: 11px;\n}\n/* register title design */\n.register-title{\n    font-family: myTitle;\n    color: white;\n    font-size: 45px;\n    margin: 0;\n    padding: 0;\n    text-shadow: 2px 2px 3px rgb(32, 9, 20);\n}\n/* Register top div */\n.register-top-div{\n    height: 90%;\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) { \n    \n    /* td design with inputs */\n    .input-td{\n        padding-left: 10px;\n    }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"