export default "/* Title design */\nh2{\n    color: white;\n    font-family: myTitle;\n    font-size: 40px;\n    text-shadow: 2px 2px 3px rgb(39, 12, 23);\n}\n/* table, tr, td desgin */\ntable, tr, td{\n    text-align: center;\n}\n/* table design */\ntable{\n    margin-top: 30px;\n}\n/* td design */\ntd{\n    height: 30px;\n    width: 250px;\n}\n/* Error message design */\n.err-msg{\n    color: white;\n    display: block;\n    font-weight: 500;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding-left: 0px 3px;\n    font-size: 11px;\n}\n/* button design */\nbutton{\n    margin:auto;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n    margin-left: 8px;\n    text-align: center;\n}\n/* button design disabled */\nbutton:disabled{\n    margin:auto;\n    color: rgb(146, 146, 146);\n    background-image: radial-gradient( rgb(121, 93, 101), rgb(88, 54, 63));\n    border-radius: 5px;\n    margin-left: 8px;\n    text-align: center;\n}\n/* Login input design */\n.login-input{\n    width:190px;\n    margin: 7px;\n    height: 26px;\n    border-radius: 3px;\n    box-shadow: 3px 2px 5px rgb(53, 22, 31);\n    border: none;\n    padding: 3px;\n    background-color: rgba(255, 255, 255, 0.589);\n    text-align: left;\n}\n/* text next to check box design */\n.checkbox-text{\n    color: white;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding-left: 0px 3px;\n    font-size: 13px;\n}\n/* Text above register button design */\np{\n    color: white;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding-left: 0px 3px;\n    font-size: 13px;  \n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Title design */\n    h2 {\n        font-size: 30px;\n    }\n}\n/* Design for screen larger than 575px and smaller than 991px (including 991px) */\n@media screen and (max-width: 991px) and (min-width: 576px) {\n    \n    /* Login input design */\n    .login-input{\n        width:150px;   \n    }\n\n    /* td desgin */\n    td{\n        width: 160px;\n    }\n\n    /* text next to check box design */\n    .checkbox-text{\n        padding: 0;\n        margin: 0;\n        font-size: 12px;\n    }\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"