export default "/* Top div */\nsection{\n    margin:0px;\n    padding:15px;\n    display: grid;\n    border: 1px solid gray;\n    grid-template-rows: 15% 80%;\n    grid-template-columns: repeat(12,1fr);\n    text-align: center;\n    box-sizing: border-box;\n    height:100%;\n}\n/* header design */\nheader{\n    grid-column: span 12;\n    position: relative;\n}\n/* main design */\nmain{\n    grid-column: span 12;\n    overflow-y: auto;\n    overflow-x: hidden;\n    height: 100%;\n}\n/* Contact us div */\n.contact-div{\n    display: inline-block;\n    position: absolute;\n    right: 20px;\n}\n/* Contact us text design */\n.contact-us{\n    margin: 2px;\n    padding: 2px;\n    font-size: 14px;\n    color: rgb(255, 255, 255);\n    text-shadow: 2px 2px 3px rgb(26, 7, 13);\n}\n/* Contact us title */\n.contact-us-title{\n    font-size: 30px;\n    color: white;\n    font-family: myTitle;\n    margin-top: 0;\n    margin-bottom: 0;\n}\n/* Logi div: */\n.logo-div{\n    display: inline-block;\n    position: absolute;\n    left: 20px;\n}\n/* Email icon design */\n.email-icon{\n    width: 20px;\n    height: 20px;\n    -webkit-filter: brightness(800%) drop-shadow(2px 2px 3px rgb(31, 8, 19));\n            filter: brightness(800%) drop-shadow(2px 2px 3px rgb(31, 8, 19)); \n}\n/* Phone icon design  */\n.phone-icon{\n    width: 20px;\n    height: 20px;\n    -webkit-filter: invert(100%) brightness(1000%) drop-shadow(2px 2px 3px rgb(31, 8, 19));\n            filter: invert(100%) brightness(1000%) drop-shadow(2px 2px 3px rgb(31, 8, 19));\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Contact us title */\n    .contact-us-title{\n        font-size: 20px;\n    }\n\n    /* Contact us text design */\n    .contact-us{\n        font-size: 10px;\n    }\n\n    /* Phone icon design  */\n    .phone-icon{\n        width: 15px;\n        height: 15px; \n    }\n\n    /* Email icon design  */\n    .email-icon{\n        width: 15px;\n        height: 15px;  \n    }\n\n    /* Logo div */\n    .logo-div{\n        left: 10px;\n    }\n\n    /* Contact us div */\n    .contact-div{\n        right: 10px;\n    }\n\n    /* main design */\n    main{\n        overflow-x: hidden;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"