export default "/* Button design */\nbutton{\n    margin:2px;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* Button disabled design */\nbutton:disabled{\n    margin:0;\n    color: rgb(146, 146, 146);\n    background-image: radial-gradient( rgb(121, 93, 101), rgb(88, 54, 63));\n    border-radius: 5px;\n}\n/* Title design */\np{\n    color:white;\n    font-family: myTitle;\n    font-size: 35px;\n    margin: 0;\n    padding: 0;\n}\n/* Table, tr, td design */\ntable, tr, td{\n    margin: 0;\n    padding: 0;\n}\n/* Table design */\ntable{\n    margin: auto;\n}\n/* td with input design */\n.input-td{\n    padding: 0;\n    width: 250px;\n    height: 60px;\n    vertical-align: baseline;\n}\n/* Customized upload image button */\n.upload-image-button{\n    width: 120px;\n    height: 30px;\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 4px;\n    border-top: 2px solid white;\n    border-left: 2px solid white;\n    border-right: 2px solid rgb(80, 80, 80);\n    border-bottom: 2px solid rgb(80, 80, 80);\n}\n/* Customized upload image button when hover */\n.upload-image-button:hover{\n    width: 120px;\n    height: 30px;\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 4px;\n    border-top: 2px solid white;\n    border-left: 2px solid white;\n    border-right: 2px solid rgb(80, 80, 80);\n    border-bottom: 2px solid rgb(80, 80, 80);\n    cursor: pointer;\n}\n/* Customized upload image button when clicking on it*/\n.upload-image-button:active{\n    width: 120px;\n    height: 30px;\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 4px;\n    border-top: 2px solid rgb(80, 80, 80);\n    border-left: 2px solid rgb(80, 80, 80);\n    border-right: 2px solid white;\n    border-bottom: 2px solid white;\n    cursor: pointer;\n}\n/* Error message design */\n.err-msg{\n    color: white;\n    display: block;\n    font-weight: 500;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding-left: 0px 3px;\n    font-size: 11px;\n}\n/* Input and Select boxs design */\ninput, select{\n    width:190px;\n    height: 30px;\n    border-radius: 3px;\n    box-shadow: 3px 2px 5px rgb(53, 22, 31);\n    border: none;\n    background-color: rgba(255, 255, 255, 0.589);\n}\n/* Doesn't display the file select button and the text next to it */\ninput[type=\"file\"]{\n    display: none;\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"