export default "/* Order div */\n.order-div{\n    border: 1px solid rgb(133, 132, 132);\n    width:70%;\n    margin-top: 10px;\n    height:83%;\n    float: left;\n    overflow-y: auto;\n    box-shadow: 3px 3px 5px rgb(48, 48, 48);\n    border-radius: 7px;\n}\n/* Cart div */\n.cart-div{\n    border: 1px solid rgb(153, 153, 153);\n    float: left;\n    width:25%;\n    margin: 10px;\n    height:83%;\n    overflow-y: auto;\n    box-shadow: 3px 3px 5px rgb(88, 88, 88);\n    border-radius: 7px;\n}\n/* button design */\n.order-button, .cancel-button{\n    margin:0;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* button disabled desing  */\n.order-button:disabled{\n    margin:0;\n    color: rgb(146, 146, 146);\n    background-image: radial-gradient( rgb(121, 93, 101), rgb(88, 54, 63));\n    border-radius: 5px;\n}\n/* Hello customer title div */\n.top-page-div{\n    width: 50px;\n    height: 50px;\n    margin:2px;\n    padding: 2px;\n}\n/* Hello customer design */\n.hello-class{\n    display: block;\n    position: absolute;\n    left: 100px;\n    padding:2px;\n    margin: 2px;\n    font-family: myTitle;\n    font-size: 35px;\n    color: white;\n}\n/* Order title */\n.order-title{\n    color: white;\n    font-family: myTitle;\n    font-size: 30px;\n    text-shadow: 2px 2px 3px rgb(36, 8, 18);\n}\n/* Inputs labels */\n.order-lable{\n    width:190px;\n    margin: 2px;\n    padding: 0;\n    color: white;\n    display: block;\n    font-weight: 500;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11);\n    font-size: 11px;\n}\n/* input and select design */\ninput, select{\n    width:190px;\n    height: 26px;\n    border-radius: 3px;\n    box-shadow: 3px 2px 5px rgb(53, 22, 31);\n    border: none;\n    padding: 3px;\n    background-color: rgba(255, 255, 255, 0.589);\n}\n/* table, tr, td design */\ntable, tr, td{\n    margin: auto;\n    padding-bottom: 5px;\n}\n/* td with inputs design */\n.input-td{\n    padding-left: 10px;\n}\n/* error message design */\n.err-msg{\n    color: white;\n    display: block;\n    font-weight: 500;\n    text-shadow: 1px 1px 2px rgb(24, 6, 11); \n    padding: 0px 3px;\n    font-size: 11px;\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Hello customer design */\n    .hello-class{\n        margin: auto;\n        padding: 0;\n        font-size: 30px;\n        display: block;\n        width: 90%;\n        position: relative;\n        left: 10px;\n    }\n\n    /* Hello customer title div */\n    .top-page-div{\n        width: 85%;\n       \n    }\n\n    /* Cart div */\n    .cart-div{\n        width: 85%;\n        \n    }\n\n    /* Order div */\n    .order-div{\n        width: 85%;\n        margin-left: 10px;  \n    }\n\n    /* table, tr, td design */\n    table, tr, td{\n        margin: auto;\n        padding-bottom: 5px;\n    }\n    \n    /* td with inputs design */\n    .input-td{\n        padding-left: 10px;\n    }\n}\n/* Design for screen larger than 575px and smaller than 991px (including 991px) */\n@media screen and (max-width: 991px) and (min-width: 576px) {\n    \n    /* Cart div */\n    .cart-div{\n        width: 35%;\n        height:500px;\n        display: inline-block;\n    }\n\n    /* Order div */\n    .order-div{\n        width: 55%;\n        height: 500px;\n        margin-left: 5px;\n    }\n\n    /* Hello customer design */\n    .hello-class{\n        position: relative;\n        width: 250px;\n        left: 20px;\n    }\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"