export default "\n/* The container div of the component: */\n.master-div{\n    position: relative;\n    height:100%;\n}\n/*Hello and Search div:  */\n.top-page-div{\n    margin:2px;\n    padding: 2px;\n}\n/* Product's div */\n.product-div{\n    display: inline-block;\n    border: 2px solid rgb(187, 187, 187);\n    box-shadow: 2px 2px 3px rgb(97, 97, 97);\n    background-color: rgba(255, 255, 255, 0.37);\n    margin: 5px;\n    padding: 5px;\n    width: 200px;\n    height:160px;\n    border-radius: 5px;   \n    cursor: pointer; \n}\n/* Product's div when hover*/\n.product-div:hover{\n    display: inline-block;\n    border: 2px solid rgb(211, 211, 211);\n    box-shadow: 2px 2px 3px rgb(97, 97, 97);\n    background-color: rgba(255, 255, 255, 0.678);\n    margin: 5px;\n    padding: 5px;\n    width: 200px;\n    height:160px;\n    border-radius: 5px;   \n    cursor: pointer; \n}\n/* Section - display all products */\n.products-section{\n    overflow-y: auto;\n    height:70%;\n    margin: 20px 5px;\n}\n/* Product's image */\n.product-image{\n    width:100px;\n    height: 100px;\n    -webkit-filter: drop-shadow(2px 2px 3px rgb(29, 5, 14));\n            filter: drop-shadow(2px 2px 3px rgb(29, 5, 14));\n}\n/* Product's text */\n.product-p{\n    color: rgb(51, 20, 20);\n    font-size: 14px;\n    margin: 2px;\n}\n/* Categories div */\n.categories-div{\n    overflow-x: auto;\n    overflow-y: hidden;\n    height: 15%;\n    margin: 5px;\n}\n/* Categories buttons */\n.category-button{\n    margin:2px;\n    color: white;\n    background-image: radial-gradient( rgb(121, 50, 74),  rgb(126, 39, 59));\n    border-radius: 5px;\n}\n/* Section of products and categories together */\n.products-master-div{\n    border: 1px solid rgb(133, 132, 132);\n    width:70%;\n    margin-top: 10px;\n    height:85%;\n    float: left;\n    box-shadow: 3px 3px 5px rgb(48, 48, 48);\n    border-radius: 7px;\n}\n/* Sidebar div */\n.sidebar-div{\n    border: 1px solid rgb(153, 153, 153);\n    float: left;\n    width:25%;\n    margin: 10px;\n    height:85%;\n    overflow-y: auto;\n    box-shadow: 3px 3px 5px rgb(88, 88, 88);\n    border-radius: 7px;\n}\n/* Default search button */\n.search-button{\n    visibility: none;\n}\n/* Add button */\n.add-button{\n    width:30px;\n    height:30px;\n    text-align: center;\n    -webkit-filter: brightness(200%);\n            filter: brightness(200%);\n    margin: 3px;\n    padding-top: 3px;\n}\n/* Add button hover*/\n.add-button:hover{\n    width:35px;\n    height:35px;\n    text-align: center;\n    -webkit-filter: brightness(200%);\n            filter: brightness(200%);\n    cursor: pointer;\n    margin: 3px;\n    padding-top: 1px;\n}\n/* Add button div*/\n.add-button-div{\n    height:40px;\n}\n/* Hello Admin title */\n.hello-class{\n    display: inline-block;\n    position: absolute;\n    left: 55px;\n    padding:2px;\n    margin: 2px;\n    font-family: myTitle;\n    font-size: 35px;\n    color: white;\n}\n/* Search div */\n.search-class{\n    display: inline-block;\n    padding:2px;\n    margin: 2px;\n}\n/* Sorry title- when no products were found on search */\n.sorry{\n    color: white;\n    font-family: myTitle;\n    font-size: 40px;\n    padding: 0;\n    margin: 0;\n}\n/* Text- when no products were found on search */\n.no-result{\n    color: white;\n    padding: 0;\n    margin: 0;\n    font-size: 18px;\n    text-shadow: 2px 2px 3px rgb(24, 6, 14);\n}\n/* Design for screen smaller than 575px */\n@media screen and (max-width: 575px) {\n\n    /* Hello Admin title */\n    .hello-class{\n        margin: auto;\n        padding: 0;\n        font-size: 30px;\n        display: block;\n        width: 80%;\n        position: relative;\n        left: auto;\n    }\n\n    /* Search div */\n    .search-class {\n        display: block;\n        margin: 3px;\n        width: 80%;\n    }\n\n    /* Sidebar div */\n    .sidebar-div{\n        width: 85%;\n        height:280px;\n    }\n\n    /* Section of products and categories together */\n    .products-master-div{\n        width: 85%;\n        margin-left:3px ;\n    }\n\n    /* Categories div */\n    .categories-div{\n        height: 20%;\n        overflow-y: auto;\n    }\n\n    /* Product's div */\n    .product-div{\n        width: 175px;\n    }\n    \n    /* Product's div hover */\n    .product-div:hover{\n        width:175px;\n    }\n\n}\n/* Design for screen larger than 575px and smaller than 991px (including 991px) */\n@media screen and (max-width: 991px) and (min-width: 576px) {\n    /* Sidebar div */\n    .sidebar-div{\n        width: 35%;\n        height: 80%;\n    }\n\n    /* Section of products and categories together */\n    .products-master-div{\n        width: 55%;\n        height: 80%;\n        margin-left: 5px;\n    }\n\n    /* Product's div */\n    .product-div{\n        width: 160px;\n        height: 140px;\n    }\n    \n    /* Product's div hover*/\n    .product-div:hover{\n        width:160px;\n        height: 140px;\n    }\n\n    /* Product's text */\n    .product-p{\n        font-size: 10px;\n    }\n\n    /* Search div */\n    .search-class{\n        padding-left: 100px;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"